<template>
    <div class="app">
        <div class="todoList">
            What be to do done!
            <div class="form-group">
                <input type="text" class="form-control" placeholder="Enter your task" v-model="newTodo"
                    @keyup.enter="addTodo">
            </div>
            <div class="alert" v-for="(todo,index) in todos" :key="todo.id">
                <div @click="remove(index)"><i class="fas fa-trash"></i></div>
                <input type="checkbox" class="form-check-input" v-model="todo.completed">
                <div v-if="!todo.edit" @dblclick="editTodo(todo)" :class="{completed: todo.completed}">{{todo.title}}
                </div>
                <input type="text" class="form-control custom" v-else v-model="todo.title" @blur="doenEnter(todo)"
                    @keyup.enter="doenEnter(todo)" v-focus @keyup.esc="cancelEdit(todo)">
            </div>
            <div class="form-group custom-check">
                <label> {{remening}} items completed <i class="fas fa-check"></i></label>
            </div>
            <!--
                 <div class="btn-group">
                 <button type="button" :class="{active:filter = 'all'}" 
                 @click="filter = 'all'">all</button>
                 <button type="button" :class="{active:filter = 'active'}" 
                 @click="filter ='active'">active</button>
                 <button type="button" :class="{active:filter = 'completed'}" 
                 @click="filter ='completed'">completed</button>
             </div>
             -->
            <div class="btn-group">
                <button type="button" v-if="showClear" @click="completedTask">clear Completed</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "todoList",
        data() {
            return {
                newTodo: "",
                Forid: 3,
                berforeCash: "",
                filter: "all",
                todos: [{
                        id: 1,
                        title: "finished the vue project",
                        completed: false,
                        edit: false
                    },
                    {
                        id: 2,
                        title: "take over world",
                        completed: false,
                        edit: false
                    },
                ]
            }
        },
        directives: {
            focus: {
                // directive definition
                inserted: function (el) {
                    el.focus()
                }
            }
        },
        computed: {
            remening() {
                return this.todos.filter(todo => !todo.completed).length;
            },
            showClear() {
                return this.todos.filter(todo => todo.completed).length > 0;
            }
            /*
            todosFilter(){
                if(this.filter = "all"){
                    return this.todos;
                }else if(this.filter = "active"){
                    return this.todos.filter(todos => !todo.completed);
                }else if(this.filter = "completed"){
                    return this.todos.filter(todo => todo.completed);
                }
                return this.todos;
            }
            */
        },
        methods: {
            addTodo() {
                this.todos.push({
                    id: this.Forid,
                    title: this.newTodo,
                    completed: false
                })
                this.newTodo = '',
                    this.Forid++;
            },
            remove(index) {
                this.todos.splice(index, 1);
            },
            editTodo(todo) {
                this.berforeCash = todo.title;
                todo.edit = true;
            },
            doenEnter(todo) {
                todo.edit = false;
            },
            cancelEdit(todo) {
                todo.title = this.berforeCash;
                todo.edit = false;
            },
            completedTask() {
                this.todos = this.todos.filter(todo => !todo.completed);
            }
        }
    }
</script>

<style lang="scss">
    .app {
        position: relative;

        .todoList {
            text-align: center;
            font-size: 20px;
            font-weight: bold;
            border-radius: 8px;
            background: #54a0ff;
            position: absolute;
            width: 400px;
            transform: translate(-50%, -50%);
            top: 50%;
            left: 50%;
            margin-top: 15rem;
            padding: 8px 10px;

            .form-group {
                margin-top: 10px !important;
            }

            .alert {
                position: relative;
                padding: 5px 25px;
                text-align: left;
                font-size: 16px;
                color: #ee5253;
                font-weight: bold;
                background: #c8d6e5;

                .fa-trash {
                    position: absolute;
                    right: 5px;
                    top: 7px;
                    cursor: pointer;
                }

                .form-check-input {
                    position: absolute;
                    left: 25px;

                }
            }

            .completed {
                text-decoration: line-through;
                color: grey;
            }

            .custom-check {
                float: right;
                color: #c23616;
            }

            .btn-group {
                button {
                    background: #dcdde1;
                    color: #000;
                    font-size: 16px;
                    padding: 5px 13px;
                    border: none;
                    border-radius: 7px;
                    margin-right: 8px;

                    .active {
                        background: #10ac84;
                    }
                }
            }

            .custom {
                margin-top: 8px;
            }
        }
    }
</style>